<template>
    <div
        class="uploader-file"
        :class="{'video-uploader-file':this.uploadType === 'Video'}"
        :status="status"
    >
        <slot
            :file="file"
            :list="list"
            :status="status"
            :paused="paused"
            :error="error"
            :response="response"
            :average-speed="averageSpeed"
            :formated-average-speed="formatedAverageSpeed"
            :current-speed="currentSpeed"
            :is-complete="isComplete"
            :is-uploading="isUploading"
            :size="size"
            :formated-size="formatedSize"
            :uploaded-size="uploadedSize"
            :progress="progress"
            :progress-style="progressStyle"
            :progressing-class="progressingClass"
            :time-remaining="timeRemaining"
            :formated-time-remaining="formatedTimeRemaining"
            :type="type"
            :extension="extension"
            :file-category="fileCategory"
        >
            <div class="uploader-file-progress" :class="progressingClass" :style="progressStyle"></div>
            <template>
                <div class="uploader-file-info">
                    <!-- <div class="uploader-file-name"><i class="uploader-file-icon" :icon="fileCategory"></i>{{file.name}}</div> -->
                    <div class="uploader-file-name">
                        <i
                            class="uploader-file-icon upload-file-cover"
                            :icon="fileCategory"
                            :class="coverImg(fileCategory)"
                        ></i>
                        <div class="file-info">
                            <p class="ellipsis">{{file.name}}</p>
                            <div
                                v-if="this.uploadType!=='File'"
                                class="file-info-sub"
                                :class="{'ellipsis-margin':this.uploadType === 'Video'}"
                            >
                                <span>大小：{{formatedSize}}</span>
                                <span style="margin-left:25px">时长：{{fileDuration}}</span>
                            </div>
                        </div>
                    </div>
                    <div
                        v-if="this.uploadType==='File'"
                        class="uploader-file-size"
                    >大小：{{formatedSize}}</div>
                    <div class="uploader-file-meta"></div>

                    <div class="uploader-file-status" v-show="status !== 'paused'">
                        <span :class="statusTextImg(status)">{{statusText}}</span>
                        <span v-show="status === 'uploading'">
                            <span>
                                <i class="upload-prograss">{{progressStyle.progress}}</i>
                            </span>
                            <!-- <em>{{formatedAverageSpeed}}</em> -->
                            <!-- <i>{{formatedTimeRemaining}}</i> -->
                        </span>
                    </div>
                    <div class="uploader-file-actions" v-show="status !== 'success'">
                        <!-- <span class="uploader-file-pause" @click="pause"></span> 暂停-->
                        <!-- 重新开始 -->
                        <!-- <span class="uploader-file-resume" @click="resume">️</span>  -->
                        <!-- 重试 -->
                        <span class="uploader-file-retry" v-if="status === 'error'" @click="retry"></span>
                        <span
                            class="uploader-file-remove"
                            v-if="status!=='uploading'"
                            @click="remove"
                        ></span>
                    </div>
                    <!-- <div class="uploader-file-actions" v-show="status !== 'success'"> -->
                    <!-- <span class="uploader-file-pause" @click="pause"></span> 暂停-->
                    <!-- 重新开始 -->
                    <!-- <span class="uploader-file-resume" @click="resume">️</span>  -->
                    <!-- 重试 -->
                    <!-- <span class="uploader-file-retry" @click="retry"></span> -->
                    <!-- <span class="uploader-file-remove" @click="remove"></span> -->
                    <!-- </div> -->
                </div>
            </template>
            <!-- this.uploadType === "Video" -->
            <!-- <template v-if="this.uploadType === 'Video'">
               <div  class="uploader-file-icon upload-file-cover"
                            :icon="fileCategory"
                            :class="coverImg(fileCategory)"></div>
            </template>-->
        </slot>
    </div>
</template>

<script>
import Uploader from "simple-uploader.js";
import events from "../common/file-events";
import { secondsToStr } from "../common/utils";

const COMPONENT_NAME = "uploader-file";

export default {
    name: COMPONENT_NAME,
    props: {
        file: {
            type: Object,
            default() {
                return {};
            }
        },
        list: {
            type: Boolean,
            default: false
        },
        uploadType: {
            type: String
        }
    },
    data() {
        return {
            response: null,
            paused: false,
            error: false,
            averageSpeed: 0,
            currentSpeed: 0,
            isComplete: false,
            isUploading: false,
            size: 0,
            formatedSize: "",
            uploadedSize: 0,
            progress: 0,
            timeRemaining: 0,
            type: "",
            extension: "",
            progressingClass: "",
            fileDuration: "" // zxb
        };
    },
    computed: {
        fileCategory() {
            // console.log(this.file, "00090000");
            if (this.uploadType !== "File") {
                let url = URL.createObjectURL(this.file.file);

                var audioElement = new Audio(url);
                console.log(audioElement);
                var duration;
                audioElement.addEventListener("loadedmetadata", _event => {
                    duration = audioElement.duration;
                    console.log(duration + "s");
                    this.fileDuration = secondsToStr(duration);
                });
            }

            const extension = this.extension;
            const isFolder = this.file.isFolder;
            let type = isFolder ? "folder" : "unknown";
            const categoryMap = this.file.uploader.opts.categoryMap;
            console.log(categoryMap);
            const typeMap = categoryMap || {
                image: ["gif", "jpg", "jpeg", "png", "bmp", "webp"],
                video: [
                    "mp4",
                    "m3u8",
                    "rmvb",
                    "avi",
                    "swf",
                    "3gp",
                    "mkv",
                    "flv"
                ],
                audio: ["mp3", "wav", "wma", "ogg", "aac", "flac"],
                // document: [
                //     "doc",
                //     "txt",
                //     "docx",
                //     "pages",
                //     "epub",
                //     "pdf",
                //     "numbers",
                //     "csv",
                //     "xls",
                //     "xlsx",
                //     "keynote",
                //     "ppt",
                //     "pptx"
                // ],
                zip: [
                    "rar",
                    "zip",
                    "cab",
                    "iso",
                    "jar",
                    "ace",
                    "7z",
                    "tar",
                    "gz",
                    "arj",
                    "lzh",
                    "uue",
                    "bz2",
                    "z"
                ],
                excel: ["xlsx", "xlsm", "xltx", "xltm", "xlsb", "xlam"],
                word: ["docx", "docm", "dotx", "dotm"],
                ppt: [
                    "pptx",
                    "pptm",
                    "ppsx",
                    "ppsx",
                    "potx",
                    "potm",
                    "ppam",
                    "ppt"
                ],
                pdf: ["pdf"],
                txt: ["txt"]
            };
            Object.keys(typeMap).forEach(_type => {
                const extensions = typeMap[_type];
                if (extensions.indexOf(extension) > -1) {
                    type = _type;
                }
            });
            return type;
        },
        progressStyle() {
            const progress = Math.floor(this.progress * 100);
            const style = `translateX(${Math.floor(progress - 90)}%)`;
            return {
                progress: `${progress}%`,
                webkitTransform: style,
                mozTransform: style,
                msTransform: style,
                transform: style
            };
        },
        formatedAverageSpeed() {
            return `${Uploader.utils.formatSize(this.averageSpeed)} / s`;
        },
        status() {
            const isUploading = this.isUploading;
            const isComplete = this.isComplete;
            const isError = this.error;
            const paused = this.paused;
            if (isComplete) {
                return "success";
            } else if (isError) {
                return "error";
            } else if (isUploading) {
                return "uploading";
            } else if (paused) {
                return "paused";
            } else {
                return "waiting";
            }
        },
        statusText() {
            const status = this.status;
            const fileStatusText = this.file.uploader.fileStatusText;
            let txt = status;
            if (typeof fileStatusText === "function") {
                txt = fileStatusText(status, this.response);
            } else {
                txt = fileStatusText[status];
            }
            return txt || status;
        },
        formatedFileDuration(timeRemaining) {
            this.fileDuration = secondsToStr(timeRemaining);
        },
        formatedTimeRemaining() {
            const timeRemaining = this.timeRemaining;
            const file = this.file;
            if (
                timeRemaining === Number.POSITIVE_INFINITY ||
                timeRemaining === 0
            ) {
                return "";
            }
            let parsedTimeRemaining = secondsToStr(timeRemaining);
            const parseTimeRemaining = file.uploader.opts.parseTimeRemaining;
            if (parseTimeRemaining) {
                parsedTimeRemaining = parseTimeRemaining(
                    timeRemaining,
                    parsedTimeRemaining
                );
            }
            return parsedTimeRemaining;
        }
    },
    watch: {
        status(newStatus, oldStatus) {
            if (
                oldStatus &&
                newStatus === "uploading" &&
                oldStatus !== "uploading"
            ) {
                this.tid = setTimeout(() => {
                    this.progressingClass = "uploader-file-progressing";
                }, 200);
            } else {
                clearTimeout(this.tid);
                this.progressingClass = "";
            }
        }
    },
    methods: {
        _actionCheck() {
            this.paused = this.file.paused;
            this.error = this.file.error;
            this.isUploading = this.file.isUploading();
        },
        pause() {
            this.file.pause();
            this._actionCheck();
            this._fileProgress();
        },
        resume() {
            this.file.resume();
            this._actionCheck();
        },
        remove() {
            this.file.cancel();
        },
        retry() {
            this.file.retry();
            this._actionCheck();
        },
        processResponse(message) {
            let res = message;
            try {
                res = JSON.parse(message);
            } catch (e) {}
            this.response = res;
        },
        fileEventsHandler(event, args) {
            const rootFile = args[0];
            const file = args[1];
            const target = this.list ? rootFile : file;
            if (this.file === target) {
                if (this.list && event === "fileSuccess") {
                    this.processResponse(args[2]);
                    return;
                }
                this[`_${event}`].apply(this, args);
            }
        },
        _fileProgress() {
            this.progress = this.file.progress();
            this.averageSpeed = this.file.averageSpeed;
            this.currentSpeed = this.file.currentSpeed;
            this.timeRemaining = this.file.timeRemaining();
            this.uploadedSize = this.file.sizeUploaded();
            this._actionCheck();
        },
        _fileSuccess(rootFile, file, message) {
            if (rootFile) {
                this.processResponse(message);
            }

            this._fileProgress();
            this.error = false;
            this.isComplete = true;
            this.isUploading = false;
        },
        _fileComplete() {
            this._fileSuccess();
        },
        _fileError(rootFile, file, message) {
            this._fileProgress();
            this.processResponse(message);
            this.error = true;
            this.isComplete = false;
            this.isUploading = false;
        },
        coverImg(fileCategory) {
            if (this.uploadType === "File") {
                // if (this.fileCategory === "document") {
                //     return "document";
                // } else
                if (this.fileCategory === "video") {
                    return "video";
                } else if (this.fileCategory === "image") {
                    return "image";
                } else if (this.fileCategory === "audio") {
                    return "audio";
                } else if (this.fileCategory === "zip") {
                    return "zip";
                } else if (this.fileCategory === "excel") {
                    return "excel";
                } else if (this.fileCategory === "word") {
                    return "word";
                } else if (this.fileCategory === "ppt") {
                    return "ppt";
                } else if (this.fileCategory === "pdf") {
                    return "pdf";
                } else if (this.fileCategory === "txt") {
                    return "txt";
                } else {
                    return "other";
                }
            } else if (this.uploadType === "Video") {
                return "video-cover";
            } else if (this.uploadType === "Audio") {
                return "audio";
            }
        },
        statusTextImg(status) {
            if (status === "success") {
                return "zxb-success";
            } else if (status === "error") {
                return "zxb-error";
            }
        }
    },
    mounted() {
        const staticProps = ["paused", "error", "averageSpeed", "currentSpeed"];
        const fnProps = [
            "isComplete",
            "isUploading",
            {
                key: "size",
                fn: "getSize"
            },
            {
                key: "formatedSize",
                fn: "getFormatSize"
            },
            {
                key: "uploadedSize",
                fn: "sizeUploaded"
            },
            "progress",
            "timeRemaining",
            {
                key: "type",
                fn: "getType"
            },
            {
                key: "extension",
                fn: "getExtension"
            }
        ];
        staticProps.forEach(prop => {
            this[prop] = this.file[prop];
        });
        fnProps.forEach(fnProp => {
            if (typeof fnProp === "string") {
                this[fnProp] = this.file[fnProp]();
            } else {
                this[fnProp.key] = this.file[fnProp.fn]();
            }
        });

        const handlers = (this._handlers = {});
        const eventHandler = event => {
            handlers[event] = (...args) => {
                this.fileEventsHandler(event, args);
            };
            return handlers[event];
        };
        events.forEach(event => {
            this.file.uploader.on(event, eventHandler(event));
        });
    },
    destroyed() {
        events.forEach(event => {
            this.file.uploader.off(event, this._handlers[event]);
        });
        this._handlers = null;
    }
};
</script>

<style>
.uploader-file {
    position: relative;
    /* min-height: 49px;
    line-height: 49px; */
    overflow: hidden;
    border-bottom: 1px solid #cdcdcd;
    padding: 15px 32px;
}
.video-uploader-file {
    padding: 8px 8px;
}
.border {
    border-bottom: 1px solid #cdcdcd;
}
.uploader-file[status="waiting"] .uploader-file-pause,
.uploader-file[status="uploading"] .uploader-file-pause {
    display: block;
}
.uploader-file[status="paused"] .uploader-file-resume {
    display: block;
}
.uploader-file[status="error"] .uploader-file-retry {
    display: block;
}
.uploader-file[status="success"] .uploader-file-remove {
    display: none;
}
.uploader-file[status="error"] .uploader-file-progress {
    /* background: #ffe0e0; */
}
.uploader-file-progress {
    position: absolute;
    width: 100%;
    height: 100%;
    background: #e2eeff;
    transform: translateX(-110%);
}
.uploader-file-progressing {
    transition: all 0.4s linear;
}
.uploader-file-info {
    position: relative;
    z-index: 1;
    height: 100%;
    overflow: hidden;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.file-info {
    display: inline-block;
    display: flex;
    flex-direction: column;
}
.uploader-file:hover {
    /* box-shadow:0px 2px 20px 0px rgba(203,218,225,1); */
}
.uploader-file-info i,
.uploader-file-info em {
    font-style: normal;
}
.uploader-file-name,
.uploader-file-size,
.uploader-file-meta,
.uploader-file-status,
.uploader-file-actions {
    float: left;
    position: relative;
    height: 100%;
}

.uploader-file-name {
    width: 430px;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    text-indent: 14px;
}
.uploader-file-icon {
    width: 24px;
    height: 24px;
    display: inline-block;
    vertical-align: top;
    margin-top: 13px;
    margin-right: 8px;
}
.uploader-file-icon::before {
    content: "📃";
    display: block;
    height: 100%;
    font-size: 24px;
    line-height: 1;
    text-indent: 0;
}
.uploader-file-icon[icon="folder"]::before {
    content: "📂";
}
.uploader-file-icon[icon="image"]::before {
    content: "📊";
}
.uploader-file-icon[icon="video"]::before {
    content: "📹";
}
.uploader-file-icon[icon="audio"]::before {
    content: "🎵";
}
.uploader-file-icon[icon="document"]::before {
    content: "📋";
}
.uploader-file-size {
    width: 180px;
    /* width: 13%; */
    /* text-indent: 10px; */
}
.uploader-file-meta {
    width: 8%;
}
.uploader-file-status {
    width: 24%;
    /* text-indent: 20px; */
}
.uploader-file-actions {
    width: 10%;
}
.uploader-file-actions > span {
    display: none;
    float: left;
    width: 16px;
    height: 16px;
    /* margin-top: 16px; */
    margin-right: 10px;
    cursor: pointer;
    /* background: url("data:image/png;base64,")
        no-repeat 0 0; */
}
.uploader-file-actions > span:hover {
    /* background-position-x: -21px; */
}
.uploader-file-actions .uploader-file-pause {
    background-position-y: 0;
}
.uploader-file-actions .uploader-file-resume {
    background-position-y: -17px;
}
.uploader-file-actions .uploader-file-retry {
    background-position-y: -53px;
}
.uploader-file-actions .uploader-file-remove {
    display: block;
    background-position-y: -34px;
}

.uploader-file-size {
    display: flex;
    align-items: center;
}
</style>
<style scoped>
/* 文件封面  begin */
.upload-file-cover {
    display: inline-block;
    width: 28px;
    height: 28px;
    flex: none;
}
.document {
    background: url("./images/document.png") no-repeat center;
    background-size: contain;
}
.video {
    background: url("./images/file/video.png") no-repeat center;
    background-size: contain;
}
.image {
    background: url("./images/file/picture.png") no-repeat center;
    background-size: contain;
}
.audio {
    background: url("./images/file/audio.png") no-repeat center;
    background-size: contain;
}
.zip {
    background: url("./images/file/zip.png") no-repeat center;
    background-size: contain;
}
.other {
    background: url("./images/file/other.png") no-repeat center;
    background-size: contain;
}
.excel {
    background: url("./images/file/excel.png") no-repeat center;
    background-size: contain;
}
.word {
    background: url("./images/file/word.png") no-repeat center;
    background-size: contain;
}
.ppt {
    background: url("./images/file/ppt.png") no-repeat center;
    background-size: contain;
}
.pdf{
    background: url("./images/file/pdf.png") no-repeat center;
    background-size: contain;
}
.txt {
    background: url("./images/file/txt.png") no-repeat center;
    background-size: contain;
}
/* 文件封面  end */
/* 视频封面 begin */
.video-cover {
    width: 190px;
    height: 134px;
    background: url("./images/video-default.png") no-repeat center;
    background-size: 100%;
}
.uploader-file-remove {
    display: inline-block;
    width: 16px;
    height: 16px;
    background: url("./images/delete.png") no-repeat center;
    background-size: contain;
}
.uploader-file-retry {
    display: inline-block;
    width: 16px;
    height: 16px;
    background: url("./images/retry.png") no-repeat center;
    background-size: contain;
    margin-right: 16px !important;
}
.zxb-success {
    color: #63dc8c;
}
.zxb-success::before {
    display: inline-block;
    content: "";
    width: 18px;
    height: 18px;
    padding-right: 8px;
    background: url("./images/success.png") no-repeat center;
    background-size: contain;
    vertical-align: sub;
}
.zxb-error {
    color: #fb4d68;
}
.zxb-error::before {
    display: inline-block;
    content: "";
    width: 18px;
    height: 18px;
    padding-right: 8px;
    background: url("./images/failed.png") no-repeat center;
    background-size: contain;
    vertical-align: sub;
}
.upload-prograss {
    color: #f15533;
}
.uploader-list .uploader-file-status {
    padding: 0;
}
.ellipsis {
    display: inline-block;
    width: 200px;
    overflow: hidden; /*超出部分隐藏*/
    text-overflow: ellipsis; /* 超出部分显示省略号 */
    white-space: nowrap; /*规定段落中的文本不进行换行 */
    color: #262626;
    line-height: 22px;
}

.file-info-sub {
    margin-top: 10px;
}
.ellipsis-margin {
    margin-top: 30px;
}
.file-info-sub span {
    color: #a1a8b1;
}
</style>



